import React from 'react'
import { Heading } from 'theme-ui'
import Typewriter from 'typewriter-effect'

/**
 * Shadow me to add your own content
 */

const styles = {
  heading: {
    span: {
      color: `omegaDark`,
    },
  },
}

export default () => (
  <Heading variant='h2' sx={styles.heading}>
    <Typewriter
      options={{
        autoStart: true,
        loop: true,
      }}
      onInit={(typewriter) => {
        typewriter
          .pauseFor(2500)
          .typeString('')
          .pauseFor(1300)
          .deleteChars(4)
          .typeString('<strong></strong>')
          .pauseFor(1300)
          .deleteChars(4)
          .typeString('<strong></strong>')
          .pauseFor(1300)
          .deleteChars(4)
          .typeString(
            '<strong><span style="color: #F3C70D;">$$</span></strong>',
          )
          .pauseFor(3000)
          .start()
      }}
    />
  </Heading>
)
